<template>
  <div>
    <el-button type="primary" @click="$router.push('role/add')">添加<i class="el-icon-circle-plus-outline"></i></el-button>
    <div class="table-bg">
      <el-table :data="list" style="width: 100%;margin-bottom: 20px;">
        <el-table-column label="角色ID" prop="id"></el-table-column>
        <el-table-column label="角色名称" prop="rolename"></el-table-column>
        <el-table-column label="状态" prop="status">
          <template slot-scope="scope">
              <el-tag v-if="scope.row.status==1" type="success">正常</el-tag>
              <el-tag  v-else type="info">禁用</el-tag>
            </template>
        </el-table-column>
        <el-table-column label="操作" prop="status">
            <template slot-scope="scope">
              <el-button @click="$router.push('/role/edit/'+scope.row.id)"  icon="el-icon-edit"  type="success" circle size="small"></el-button>
              <el-button @click="see(scope.row)"  icon="el-icon-view"  type="warning" circle size="small"></el-button>
              <el-button @click="del(scope.row.id)" type="danger" icon="el-icon-delete" circle size="small"></el-button>
            </template>
        </el-table-column>
      </el-table>

      <!-- 弹框 -->
      <el-dialog  title="角色信息"  :visible.sync="dialogVisible"  width="38%" >
        <el-row>
          <el-col :span="4" :offset="5"><b>角色名称：</b></el-col>
          <el-col :span="12">{{forminfo.rolename}}</el-col>
        </el-row>
        <br><br>
        <el-row>
          <el-col :span="4" :offset="5"><b>权限节点：</b></el-col>
          <el-col :span="12">
              <el-tree
                  ref="tree"
                  :data="menulist"
                  show-checkbox
                  node-key="id"
                  :check-strictly="isStrictly"
                  default-expand-all
                  :props="{ children: 'children',label: 'title'}">
              </el-tree>
          </el-col>
        </el-row>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters,mapActions }  from "vuex"
export default {
  data() {
    return {
      dialogVisible:false,
      forminfo:{
         rolename:"",
         menus:[]
      },
      isStrictly:true
    };
  },
  computed: {
    ...mapGetters({
        list:"role/rolelist",
        menulist:"menu/menulist"
    })
  },
  mounted() {
    if(!this.list.length){
      this.getRoleList();
    }
    if(!this.list.menulist){
      this.getMenuList();
    }
  },
  methods: {
      ...mapActions({
        getRoleList:"role/getlist",
        getMenuList:"menu/getlist",
        del:"role/del"
      }),
      see(val){
          this.dialogVisible = true
          this.forminfo = val;
          this.$nextTick(()=>{  // 确保弹框节点渲染完成！
            let menuKeys = val.menus.split(","); 
            this.$refs.tree.setCheckedKeys(menuKeys);
          })
      }
  },
  components: {}
};
</script>
<style scoped>
</style>


